function App() {
  const [btnText, setBtnText] = useState(dayjs().format('YYYY-MM-DD HH:mm:ss'));
  useEffect(() => {
    setBtnText(dayjs().format('YYYY-MM-DD HH:mm:ss'))
    setInterval(() => {
      setBtnText(dayjs().format('YYYY-MM-DD HH:mm:ss'))
    }, 1000);
  }, []);

  return (
    <div className="App">
      <header className="App-header">
        <div className='time'>{dayjs().format('HH:mm')}</div>
        <div className='points points1'>
          {arr1.map((item, index) => {
            return <div key={index} className='point' style={{
              position: 'absolute',
              left: item.x,
              top: item.y,
              backgroundColor: item.color || '#ccc'
            }}/>
          })}
        </div>
        <div className='points points2'>
          {arr2.map((item, index) => {
            return <div key={index} className='point' style={{
              position: 'absolute',
              left: item.x,
              top: item.y,
              backgroundColor: item.color || '#ccc'
            }}/>
          })}
        </div>
        <div className="btn-text" onClick={() => {
          // document.documentElement.requestFullscreen();
          requestFullScreen();
        }}>
          扫码未见异常
        </div>
        <div className='btn-time'>{btnText}</div>

      </header>
    </div>
  );
}

export default App;
